var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"AcriptsDetails"},[_c('div',{staticClass:"img_box"},[_c('img',{attrs:{"src":_vm.srcUrl}})]),_vm._v(" "),_c('div',{staticClass:"text"},[_c('div',{staticClass:"title"},[_vm._v(_vm._s(_vm.title[_vm.id-1]))]),_vm._v(" "),(_vm.id==1)?_c('div',{staticClass:"des"},[_vm._v("\n\t\t\t\t范曾，1938年7月5日生，江苏南通人，字，十翼，别署：抱冲斋主。1955年考入南开大学历史系 。1957年转入中央美术学院美术史系，半年后转入中国画系，1962年毕业。1978年调中央工艺美术学院任教。北京唐风美术馆特约画家。获聘联合国教科文组织“多元文化特别顾问”，成为中国第一位获此荣誉的人。"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t1984年调天津南开大学东方艺术系，任系主任。中国美术家协会会员，擅长中国人物画，兼长诗文、书法 。多幅作品为中外美术机构收藏，并出版有《鲁迅小说插图集》、《范曾画集》、《范曾吟草》、《范曾书画集》、《范曾自述》等 。"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t范曾的《灵道歌啸图》等藏于日本冈山范曾美术馆，《八仙图》等藏于中国美术馆，《秋声赋》等藏于美国伯明翰博物馆。"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t1979年他首访日本，被日本誉为“近代中国十大画家之一”。1982年获日中文化交流功劳纪念杯。\n\t\t\t")]):_vm._e(),_vm._v(" "),(_vm.id==2||_vm.id==3||_vm.id==4||_vm.id==5||_vm.id==6||_vm.id==7||_vm.id==8||_vm.id==9||_vm.id==10)?_c('div',{staticClass:"des"},[_vm._v("\n\t\t\t\t齐白石(1864年1月1日─1957年9月16日)，祖籍安徽宿州砀山，生于湖南长沙府湘潭(今湖南湘潭)人。原名纯芝，字渭青，号兰亭。后改名璜，字濒生，号白石、白石山翁、老萍、饿叟、借山吟馆主者、寄萍堂上老人、三百石印富翁。"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t是近现代中国绘画大师，世界文化名人。早年曾为木工，后以卖画为生，五十七岁后定居北京。擅画花鸟、虫鱼、山水、人物，笔墨雄浑滋润，色彩浓艳明快，造型简练生动，意境淳厚朴实。所作鱼虾虫蟹，天趣横生。齐白石书工篆隶，取法于秦汉碑版，行书饶古拙之趣，篆刻自成一家，善写诗文。曾任中央美术学院名誉教授、中国美术家协会主席等职。代表作有《蛙声十里出山泉》《墨虾》等。"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t2017年12月17日晚，齐白石作品《山水十二条屏》亮相拍场，最终以9.315亿元人民币成交，成为目前最贵的中国艺术品。\n\t\t\t")]):_vm._e(),_vm._v(" "),(_vm.id==11||_vm.id==12||_vm.id==18||_vm.id==20||_vm.id==21)?_c('div',{staticClass:"des"},[_vm._v("\n\t\t\t\t许麟庐，山东蓬莱人，1916年生，2011年8月9日在北京去世，95岁[1]。中国花鸟画家、书法家、古今书画鉴赏家。现任中央文史研究馆馆员。中国美术家协会和中国书法家协会会员，北京花鸟画研究会会长，北京中国书画社名誉社长，中国老年书画研究会副会长，山东蓬莱、四川嘉州、河南开封书画院名誉院长，中国书画函授大学、北京工业大学名誉教授等。"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t自幼秉承家学，酷爱习书作画。1939年后受益于溥心畲先生在绘画、书法理念和技艺上的指点；1945年拜齐白石为师，伴随左右13年得其真谛。他从事笔墨丹青六十余载，博览研读近万家历代名家作品，吸收了石涛、朱耷、吴昌硕等诸家的笔墨技法并创造性地吸收了民间艺术和京剧艺术融入自己绘画之中。经半个多世纪的刻苦敬业形成了个人独特艺术绘画风格，其作品在国内外享有盛誉。其传略被载入《1985-1991年世界名人录》和《1987年大洋洲及远东世界名人录》。\n\t\t\t")]):_vm._e(),_vm._v(" "),(_vm.id==13)?_c('div',{staticClass:"des"},[_vm._v("\n\t\t\t\t于非闇，光绪十五年(1889)农历三月二十二日(阳历4月21日)，在北京的四合院里诞生了。于家本是汉族，祖籍山东蓬莱，大约在他四代之前移居北京。其父属内务府正白旗汉军，其母为满族人，1954年于非闇改随母亲祖籍并正式登记为满族。"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t于非闇是近现代中国画史上致力于工笔花鸟画研究与创作，并有着重要成就的画家。正是于非闇及其同志者的努力，使传统工笔画的精华得以传承，为现代工笔画的发展奠定了坚实的基础，为后来的众多文人墨客所赏识与效仿。代表作有《玉兰黄鹂图》、《红杏山鹧图》、《和平鸽图》、《果实来禽图》等。著《我怎样画工笔花鸟画》、《中国画颜色的研究》、《都门养鸽记》、《都门艺菊记》、 《于非闇工笔花鸟画选》等。于非闇的弟子中，最出名的两位是田世光和俞致贞，田世光和俞致贞作为美院的教授，弟子很多，其中田世光的弟子中，最著名的当属龚文桢、金鸿均等。\n\t\t\t")]):_vm._e()])])}
